.tile {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1.333rem;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;

  &.tile-active:hover {
    opacity: 0.5;
    cursor: pointer;
  }
}

.tile-visible {
  &.tile-active:hover {
    opacity: 1;
    cursor: default;
  }
  .tile-neighbors {
    position: absolute;
    font-size: 50%;
    font-weight: bold;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -66%);
    line-height: 1;
  }
}
